.App {
  text-align: center;
}

input,button{
    border: none;
    padding: 5px;
    border-radius: 8px;
    width: calc(60% - 20px);
    box-sizing: border-box;
    margin-right: 20px;
    margin-top: 10px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.coll{
  display: flex;
  flex-direction:column;
  width: 80%;
  justify-content:center;
  align-items:center;

}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 5px 10px;
}

.App-link {
  color: #61dafb;
}


